@import "../variables/variables";
@import "../mixins/font";
@import "reset";
@import "fonts";
@import "img";
@import "link";

.g-container {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	height: 100%;
	max-width: 1920px;
	// background: url(../images/fonmain.jpg);
	background-color: rgb(0, 0, 0);
	margin: 0 auto;
}

body {
	padding: 0;
	margin: 0;
	font-family: 'Roboto';
	font-size: 14px;
	color: $main_color_text;
	
	
}

h1,h2,h3,h4,h5,h6 {
	padding: 0;
	margin: 0;
}
.wrapper{
	
	max-width: 1200px;
	width: 100%;
	height: 100%;
	margin: 0 auto;
	// border: 1px solid white;
}

.header {
	margin-top: 15px;
	background: rgba(171, 168, 168, 0.687);
	box-shadow: 0px 4px 14px 17px rgba(238, 9, 9, 0.25);
	border-radius: 20px;
	display: flex;
	align-items: center;
	padding: 0 15px;
	
}
.header__logo{
	max-width: 180px;
	min-width: 30px;
	padding: 5px 0;
	width: 100%;
	margin-right: 100px;
	margin-left: 15px;
	transition: 1s;
	&:hover{
		transform: rotate(360deg)
	}
	@media screen and (max-width: 790px) {
		margin-right: 50px;
	}
	@media screen and (max-width: 600px) {
		margin-right: 25px;
	}
}
.header__menu{
	max-width: 900px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	
}
.header__menu-list{
	margin-right: 25px;
	&:last-child{
		margin-right: 0;
	}
	a{
		white-space: nowrap;
		color: black;
		font-family: 'Roboto';
		font-size: 25px;
		text-transform: uppercase;
		font-weight: 600;
		transition: 0.5s;
		@media screen and (max-width: 1100px) {
			font-size: 17px;	
		}
		@media screen and (max-width: 890px) {
			font-size: 14px;	
		}
		@media screen and (max-width: 790px) {
			font-size: 12px;	
		}
		@media screen and (max-width: 540px) {
			font-size: 8px;
		}
		@media screen and (max-width: 400px) {
			font-size: 6px;
		}
	}
	
	a:hover{
		color: rgb(213, 44, 44);
		font-size: 30px;
	}
	
}

