.swiper {
	width: 100%;
	height: 100%;
	
	
  }

  .swiper-slide {
	
	// text-align: center;
	font-size: 20px;
	border-radius: 20px;
	background: rgba(255, 10, 10, 0.718);

	/* Center slide text vertically */
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
	@media screen and (max-width: 1000px) {
		flex-direction: column;
		}
		@media screen and (max-width: 1000px) {
			display: none;
			}
		
	
  }
  

  .swiper-slide img {
	display: block;
	padding: 15px 15px;
	max-width: 800px;
	max-height: 500px;
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-radius: 30px;
	@media screen and (max-width: 851px) {
		display: none;
		
		}
  }
